import NextAuth from "next-auth";
import GoogleProvider from "next-auth/providers/google";
import InstagramProvider from "next-auth/providers/instagram";
import { shopifyRequest } from "../../../../utils/shopify";
import { CUSTOMER_CREATE, CUSTOMER_LOGIN } from "../../../../queries/customer";

const handler = NextAuth({
  providers: [
    GoogleProvider({
      clientId: process.env.GOOGLE_CLIENT_ID,
      clientSecret: process.env.GOOGLE_CLIENT_SECRET,
    }),
    InstagramProvider({
      clientId: process.env.INSTAGRAM_CLIENT_ID,
      clientSecret: process.env.INSTAGRAM_CLIENT_SECRET,
    }),
  ],

  callbacks: {
    async signIn({ user, account, profile }) {
      if (account.provider === "google" || account.provider === "instagram") {
        try {
          // Check if customer exists in Shopify or create new one
          const email = user.email;

          // Handle name extraction based on provider
          let firstName = "User";
          let lastName = "";

          if (account.provider === "google") {
            firstName = user.name?.split(" ")[0] || "User";
            lastName = user.name?.split(" ").slice(1).join(" ") || "";
          } else if (account.provider === "instagram") {
            // Instagram might provide username instead of full name
            firstName = profile?.username || user.name?.split(" ")[0] || "User";
            lastName = user.name?.split(" ").slice(1).join(" ") || "";
          }

          // Generate random password for OAuth users
          const randomPassword = Math.random().toString(36).slice(-12) + "Aa1!";

          try {
            await shopifyRequest(CUSTOMER_CREATE, {
              input: {
                email,
                password: randomPassword,
                firstName,
                lastName,
              },
            });
            console.log(`Customer created via ${account.provider}`);
          } catch (error) {
            // Customer might already exist, that's okay
            console.log(
              `Customer might already exist (${account.provider}):`,
              error.message
            );
          }

          return true;
        } catch (error) {
          console.error(
            `Error creating Shopify customer via ${account.provider}:`,
            error
          );
          return false;
        }
      }
      return true;
    },

    async jwt({ token, account, user, profile }) {
      // Store user info in token
      if (account && user) {
        token.email = user.email;
        token.name = user.name;
        token.picture = user.image;
        token.provider = account.provider;

        // Store Instagram-specific data if available
        if (account.provider === "instagram" && profile) {
          token.username = profile.username;
        }
      }
      return token;
    },

    async session({ session, token }) {
      // Make user info available in session
      session.user.email = token.email;
      session.user.name = token.name;
      session.user.image = token.picture;
      session.user.provider = token.provider;

      if (token.username) {
        session.user.username = token.username;
      }

      return session;
    },
  },

  pages: {
    signIn: "/login",
    error: "/login",
  },

  debug: true, // Enable debug logs

  secret: process.env.NEXTAUTH_SECRET,
});

export { handler as GET, handler as POST };
